body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.headerContainer{
display: flex;
flex-direction: column;
border: gray solid 5px;
background-color: black;
color: green;
align-items: center;
}
.title{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 40px;
  font-weight: bold;
  font-style: oblique;
  flex:1 0 100%;
}
.navLink{
  color:green;
  font-weight: bold;
  font-style: none;
  text-decoration: none
}

.is-active{
  font-weight: bold;
  color:greenyellow;
  text-decoration: underline;
}
.navbar{
  display: flex;
  justify-content: space-around ;
  border: greenyellow solid 2px;
  width: 100%;
  height: 40px;
  align-self: center;
  align-items: center;
}




.description {
  text-align: center;
  font-size: 25px;
  font-family: Georgia, 'Times New Roman', Times, serif;
}
p{
  text-align: center;
}
.amount {
  text-align: center;
  font-size: 20px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: #006e00;
  font-weight: bold;
}
.createdAt{
  font-style: italic;

}
.note{
  border: black solid 1px; 
  padding: 10px;
  width: 280px;
  margin: 0 auto;
}

.spanNote{
  font-style: italic;
  text-decoration: underline;
  font-weight: bold;
  
}
.buttonsContainer{
  display: flex;
  width: 180px;
  height: 30px;
  margin: 5px auto;
  justify-content: space-around;
  align-items: center;
}
.expenseListButtons{
padding: 5px;
font-family: 'Courier New', Courier, monospace;
font-weight: bold;
font-size: 15px;
cursor: pointer;
}

.Link{
  text-decoration: none;
font-style: none;
}

.edit{
background-color: yellow;
border: darkgoldenrod solid 1px;
border-radius: 10px;
}

.remove{
  background-color: red;
  border: darkred solid 1px;
  border-radius: 10px;
  }

