.filtersContainer{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-self: center;
  align-items: center;
  width: 100%;
  height: 60px;
  border: black solid 3px;
  background-color: gray;
}

.miniContainer{
display: flex;
flex: 1 0 50%;
width: 100%;
justify-content: space-between;
}

@media only screen and (min-width: 400px) {
  .miniContainer{
    width: 80%;
    }
}

@media only screen and (min-width: 700px) {
  .miniContainer{
    width: 70%;
    }
    
}

@media only screen and (min-width: 1000px) {
  .miniContainer{
    width: 60%;
    }
   
}
.formContainer{
  width: 100%;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  border: black solid 1px;
  background-color: hsla(0, 0%, 0%, 0.801);
}

.formInput{
  border: hsla(187, 93%, 17%, 0.478) solid 2px;
  
  width: 60%;
  height: 30px;
  font-size: 20px;
  color:black;
  padding: 5px;
}

.formTextArea{
  width: 60%;
  height: 150px;
  border: hsla(187, 93%, 17%, 0.478) solid 2px;
  font-size: 20px;
  color:black;
  padding: 5px;
}

.formButton{
  background-color: #cac1c1;
  border:#04e004 solid 1px;
  background-color: #04e004;
  border-radius: 15px;
  height:45px;
  cursor: pointer;
  color: black;
  font-weight: bold;
  font-size: 15px;
}

.formButton:hover{
  background-color: greenyellow;
  border:greenyellow solid 1px;
  border-radius: 100px;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  font-size: 20px;
}

body{
  background-color: hsl(118, 100%, 81%);
}


@media only screen and (min-width: 1000px) {
  .formInput{
    width: 50%;
  }
  .formTextArea{
    width: 50%;
  }
 
}
.createExp{
 text-align: center;
 color: black;
 font-family: 'Times New Roman', Times, serif;
}
.stickyFooter {
  display: flex;
  position: fixed;
  bottom: 0px;
  left: 0px;
  border: gray solid 2px;
  width: 100%;
  height: 5%;
  background-color: black;
  
}

.copyright{
  flex: 1 0 100%;
  color:white;
  font-size: 15px;
  font-weight: 700;
  text-align: center;
  align-self: center;
  
}
